import Affair from './affair/Affair';
import { AffairType, FilterType } from '../HW2';
import s from './Affairs.module.css';

type AffairsPropsType = {
  data: AffairType[]; // need to fix any
  setFilter: (filter: FilterType) => void
  deleteAffairCallback: (_id: AffairType['_id']) => void;
  filter: FilterType;
};

function Affairs(props: AffairsPropsType) {
  const setAll = () => {
    // need to fix
    props.setFilter('all');
  };
  const setHigh = () => {
    // need to fix
    props.setFilter('high');
  };
  const setMiddle = () => {
    // need to fix
    props.setFilter('middle');
  };
  const setLow = () => {
    // need to fix
    props.setFilter('low');
  };

  const cnAll =
    s.button + ' ' + s.all + (props.filter === 'all' ? ' ' + s.active : '');
  const cnHigh =
    s.button + ' ' + s.high + (props.filter === 'high' ? ' ' + s.active : '');
  const cnMiddle =
    s.button +
    ' ' +
    s.middle +
    (props.filter === 'middle' ? ' ' + s.active : '');
  const cnLow =
    s.button + ' ' + s.low + (props.filter === 'low' ? ' ' + s.active : '');

  const mappedAffairs = props.data.map((a: AffairType) => (
    <Affair
      key={a._id} //    99% -           
      affair={a}
      deleteAffairCallback={props.deleteAffairCallback}
    />
  ));

  return (
    <div>
      <div className={s.buttonContainer}>
        <button id={'hw2-button-all'} onClick={setAll} className={cnAll}>
          All
        </button>
        <button id={'hw2-button-high'} onClick={setHigh} className={cnHigh}>
          High
        </button>
        <button
          id={'hw2-button-middle'}
          onClick={setMiddle}
          className={cnMiddle}
        >
          Middle
        </button>
        <button id={'hw2-button-low'} onClick={setLow} className={cnLow}>
          Low
        </button>
      </div>
      <div className={s.affairs}>{mappedAffairs}</div>
    </div>
  );
}

export default Affairs;
