import {
  ChangeEvent,
  DetailedHTMLProps,
  InputHTMLAttributes,
  KeyboardEvent,
  ReactNode,
} from 'react';
import s from './SuperInputText.module.css';

//    
type DefaultInputPropsType = DetailedHTMLProps<
  InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;

//               ,  type
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = Omit<DefaultInputPropsType, 'type'> & {
  //  +       
  onChangeText?: (value: string) => void;
  onEnter?: () => void;
  error?: ReactNode;
  spanClassName?: string;
};

export const SuperInputText: React.FC<SuperInputTextPropsType> = ({
  onChange,
  onChangeText,
  onKeyPress,
  onEnter,
  error,
  className,
  spanClassName,
  id,

  ...restProps //       restProps
}) => {
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChange?.(e); //    onChange,     ( onChange  )

    onChangeText?.(e.currentTarget.value);
  };
  const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
    onKeyPress?.(e);

    if (onEnter && e.key === 'Enter') {
      onEnter();
    }

    // onEnter && //    onEnter
    //   e.key === 'Enter' && //     Enter
    //   onEnter(); //   
  };

  const finalSpanClassName = `${s.error} ${spanClassName ? spanClassName : ''}`;
  // const finalSpanClassName =
  //   s.error + (spanClassName ? ' ' + spanClassName : '');

  const finalInputClassName = `${s.input} ${
    error ? s.errorInput : s.superInput
  } ${className ? className : ''}`;
  //  const finalInputClassName = s.input +
  // (error ? ' ' + s.errorInput : ' ' + s.superInput) +
  // (className ? ' ' + className : '');
  //    

  return (
    <div className={s.inputWrapper}>
      <input
        id={id}
        type={'text'}
        onChange={onChangeCallback}
        onKeyPress={(e) => {
          onKeyPressCallback(e);
        }}
        className={finalInputClassName}
        {...restProps} //        (value   )
      />
      <span id={id ? id + '-span' : undefined} className={finalSpanClassName}>
        {error}
      </span>
    </div>
  );
};
